import styled from 'styled-components';


export const InfoSec = styled.div`
    color: #fff;
    padding: 160px 0;
    background: ${({lightBg}) => (lightBg ? '#fff' : '#ffe673')};
`

export const InfoRow = styled.div`
  display: flex;
  margin: 0 -15px -15px -15px;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: ${({ imgStart }) => (imgStart ? 'row-reverse' : 'row')};
`;

export const InfoColumn = styled.div`
  margin-bottom: 15px;
  padding-right: 15px;
  padding-left: 15px;
  flex: 1;
  max-width: 50%;
  flex-basis: 50%;

  @media (min-width: 480px) and (max-width: 1200px) {
    max-width: 100%;
    flex-basis: 100%;
    display: flex;
    justify-content: center;
  }

  @media (min-width: 320px) and (max-width: 480px) {
    max-width: 100%;
    flex-basis: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
`;

export const TextWrapper = styled.div`
  max-width: 540px;
  padding-top: 0;
  padding-bottom: 60px;

  @media (min-width: 480px) and (max-width: 1200px) {
    padding-bottom: 65px;
  }

  @media (min-width: 320px) and (max-width: 480px) {
    padding-bottom: 65px;  
  }
`;

export const ImgWrapper = styled.div`
  max-width: 555px;
  display: flex;
  justify-content: ${({ start }) => (start ? 'flex-start' : 'flex-end')};
`;

export const VideoWrapper = styled.div`
  max-width: 555px;
  width: 100%;
  height: 315px; /* Standard YouTube embed height */
  display: flex;
  justify-content: ${({ start }) => (start ? 'flex-start' : 'flex-end')};
  overflow: hidden;
  position: relative;

  iframe {
    width: 100%;
    height: 100%;
    border-radius: 10px; /* Optional: Adds rounded corners */
  }
`;



export const TopLine = styled.div`
  color: ${({ lightTopLine }) => (lightTopLine ? '#333333' : '#4B59F7')};
  font-size: 18px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  margin-bottom: 16px;
`;

export const ImgBasic = styled.img`
  padding-right: 0;
  border: 0;
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
  max-height: 500px;
`;

export const Img = styled.img`
  padding-right: 0;
  border: 0;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  display: block; /* 블록 요소로 변경하여 정렬을 깔끔하게 */
  max-height: 500px;
  border-radius: 10px; /* 모서리를 살짝 둥글게 */
  object-fit: cover; /* 이미지가 잘리는 걸 방지하고 비율 유지 */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* 은은한 그림자 효과 추가 */

  @media screen and (max-width: 768px) {
    max-height: 400px; /* 모바일에서는 조금 더 작은 높이 */
  }

  @media screen and (max-width: 480px) {
    max-height: 300px; /* 작은 화면에서는 높이 더 줄이기 */
  }
`;


export const Heading = styled.h1`
  margin-bottom: 24px;
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  color: ${({ lightText }) => (lightText ? 'Black' : '#1c2237')};
`;

export const Subtitle = styled.p`
  max-width: 480px; /* 더 넓게 */
  margin-bottom: 20px;
  font-size: 20px; /* 폰트 크기 키움 */
  line-height: 1.6; /* 줄 간격 넓혀서 가독성 개선 */
  font-weight: 500; /* 글씨 더 또렷하게 */
  color: ${({ lightTextDesc }) => (lightTextDesc ? '#333333' : '#1c2237')};
  white-space: pre-line; /* 줄바꿈 적용 */
  text-align: left;
`;
